* {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  font-style: normal;
  font-weight: bolder;
  list-style-type: none;
  text-decoration: none;
 }

 a {
  text-decoration: none;
  color: azure;
}

header nav div div ul {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  height: 100%; }

header nav div div ul li {
  position: relative;
  font-size: 120%;
  align-items: center;
  line-height: 1.7; }

header nav div div ul ul {
  position: absolute;
  display: none; }

header nav div div ul ul li {
  display: block;
  width: 120%;
  height: 100%;
  font-size: 80%;
  text-align: left;
  padding-left: 5%;
  padding-top: 5%;
  background-color: #282c34 }

li a:hover {
  color: #6effff; }

  #mainNav {
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
    background-color: #282c34;
    transition: background-color 0.2s ease; }
  
  #mainNav .navbar-brand {
    font-family: 'Poppins', sans-serif;
    font-weight: 800;
    color: #212529; }
  
  #mainNav .navbar-nav .nav-item .nav-link {
    color: azure;
    font-family: 'Poppins', sans-serif;
    font-weight: 800;
    font-size: 0.9rem;
    padding: 0.75rem 0; }
  
  
  #mainNav .navbar-nav .nav-item .nav-link.active {
    color: #6effff !important; }

    #mainNav {
      box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
      background-color: #282c34;
      transition: background-color 0.2s ease; }
    
    #mainNav .navbar-brand {
      font-family: 'Poppins', sans-serif;
      font-weight: 800;
      color: #212529; }
    
    #mainNav .navbar-nav .nav-item .nav-link {
      color: azure;
